.title{
    font-size: 23px;
    font-weight: 700;
}
.daily-item{
    background-color: #f5f5f5;
    border-radius: 15px;
    height:40px;
    margin: 5px;
    display: flex;
    cursor: pointer;
    padding: 5px 20px;
    font-size: 14px;
    align-items: center;
}
.icon-small{
    width: 40px;
}
 .day{
    cursor: inherit;
    color: #212121;
    font-weight: 600;
    margin-left: 15px;
    flex: 1 1;
    /* margin-left: 15px; */
}
.description{
  cursor: inherit;
  flex: 1 1;
  margin-right: 15px;
  text-align: right;
}
.min-max{
    color: #757575;
}
.daily-details-grid{
    display: grid;
     grid-row-gap: 0;
    grid-column-gap: 15px;
    flex: 1 1;
     column-gap: 15px; 
    grid-template-columns: auto auto;
}
.daily-details-grid-item{
    align-items: center;
    justify-content: space-between;
    display: flex;
    height: 30px;
    padding: 5px 20px;
}
.daily-details-grid-item label:first-child {
    color: #757575;
  }
  
  .daily-details-grid-item label:last-child {
    color: #212121;
  }
